import { faker } from '@faker-js/faker';
/**
 *       
 *              
 *        ,       .
 *    ,    .
 * */

/**
 *    Category,    .
 *  
 * - id ()
 * - name ()
 * - photo (, )
 *
 *  (Product) 
 * - id ()
 * - name ()
 * - photo ()
 * - desc (, )
 * - createdAt ()
 * - oldPrice (, )
 * - price ()
 * - category ()
 *
 *  (Operation)     (Cost),   (Profit)
 *
 *  (Cost) 
 * - id ()
 * - name ()
 * - desc (, )
 * - createdAt ()
 * - amount ()
 * - category ()
 * - type ('Cost')
 *
 *  (Profit) 
 * - id ()
 * - name ()
 * - desc (, )
 * - createdAt ()
 * - amount ()
 * - category ()
 * - type ('Profit')
 * */
export type Category = {
  id: number;
  name: string;
  photo?: string;
};

export type Product = {
  id: number;
  name: string;
  photo: string;
  desc?: string;
  createdAt: string;
  oldPrice?: number;
  price: number;
  category: Category;
};

export type Cost = {
  id: string;
  name: string;
  desc?: string;
  createdAt: string;
  amount: number;
  category: Category;
  type: Cost;
};

export type Profit = {
  id: string;
  name: string;
  desc?: string;
  createdAt: string;
  amount: number;
  category: Category;
  type: Profit;
};
/**
 *    (Product).
 *    ()
 * */
export const createRandomProduct = (createdAt: string): Product => ({
  id: faker.number.int({ max: 5 }),
  name: faker.commerce.product(),
  photo: faker.image.avatar(),
  desc: faker.commerce.productDescription(),
  createdAt: createdAt,
  oldPrice: +faker.commerce.price({ min: 1000, max: 5000 }),
  price: +faker.commerce.price({ min: 1000, max: 5000 }),
  category: {
    id: faker.number.int(),
    name: faker.company.name(),
    photo: faker.image.avatar(),
  },
});

/**
 *    (Operation).
 *    ()
 * */
export const createRandomOperation = (createdAt: string): Cost | Profit => ({
  id: faker.string.sample(),
  createdAt: createdAt,
  name: faker.person.fullName(),
  desc: 'desc',
  amount: faker.number.int(),
  category: {
    id: faker.number.int(),
    name: faker.company.name(),
    photo: faker.image.avatar(),
  },
  type: this,
});
